import {Component, Input, OnInit} from "@angular/core";
import {Icon} from "@util/icon";
import {VirtualObjectModel} from "@generated/virtual-object-model";
import {PrecheckReportService} from "../precheck-report.service";

@Component({
   templateUrl: "precheck-virtual-objects-tab.html",
   styleUrls: ["precheck-virtual-objects-tab.scss"],
   selector: "vsan-precheck-virtual-objects-tab",
})
export class PrecheckVirtualObjectsTabComponent implements OnInit {

   @Input()
   statusIcon: string;

   @Input()
   inaccessibleObjectsCount: number = 0;

   @Input()
   nonCompliantObjectsCount: number = 0;

   @Input()
   noDurabilityObjectsCount: number = 0;

   @Input()
   durableVirtualObjects: VirtualObjectModel[];

   @Input()
   delayTimer: number;

   @Input()
   isDurabilitySupported: boolean;

   loading: boolean;
   areAllObjectsAccessibleAndCompliant: boolean;
   reportMessage: string;

   constructor(private precheckReportService: PrecheckReportService) {
   }

   async ngOnInit(): Promise<void> {
      this.loading = true;
      this.reportMessage = this.precheckReportService.getObjectsMessage(this.inaccessibleObjectsCount,
            this.nonCompliantObjectsCount, this.noDurabilityObjectsCount);
      if (!this.statusIcon) {
         this.areAllObjectsAccessibleAndCompliant = true;
         this.loading = false;
         return;
      }

      this.loading = false;
   }

   getIconClass(): string {
      return Icon.clrIconShapeClassMap[this.statusIcon];
   }
}
