import {PrecheckDiskGroupData} from '@generated/precheck-disk-group-data';
import {PrecheckEntityType} from '@generated/precheck-entity-type';
import {DiskData} from '@generated/disk-data';

export interface PrecheckEntity {

   hostRef: any;
   type: PrecheckEntityType;
   name: string;
   primaryIconId: string;
   uuid: string;
   isHostConnected: boolean;
   isInMaintenanceMode: boolean;
   diskGroupData: PrecheckDiskGroupData;
   diskData: DiskData;
   children: PrecheckEntity[];
} //class PrecheckEntity

export module PrecheckEntity{
export class Factory {

   static create = (hostRef: any, type: PrecheckEntityType, name: string, primaryIconId: string, uuid: string, isHostConnected: boolean, isInMaintenanceMode: boolean, diskGroupData: PrecheckDiskGroupData, diskData: DiskData, children: PrecheckEntity[]): PrecheckEntity => {
      return { hostRef: hostRef, type: type, name: name, primaryIconId: primaryIconId, uuid: uuid, isHostConnected: isHostConnected, isInMaintenanceMode: isInMaintenanceMode, diskGroupData: diskGroupData, diskData: diskData, children: children, };
   }


} //class Factory

} //module PrecheckEntity
