import {Injectable} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {Icon} from "@util/icon";
import {VsanDataMigrationUtil} from "@util/vsan-data-migration-util";
import {DecommissionMode} from "@generated/decommission-mode";
import {VsanHealthData} from "@generated/vsan-health-data";
import {VsanHealthStatus} from "@generated/vsan-health-status";
import {PrecheckTaskType} from "@generated/precheck-task-type";
import {PrecheckPersistentInstanceData} from "@generated/precheck-persistent-instance-data";
import {PrecheckPersistentInstanceState} from "@generated/precheck-persistent-instance-state";
import {PrecheckResultStatusType} from "@generated/precheck-result-status-type";
import {PrecheckClusterCapacityData} from "@generated/precheck-cluster-capacity-data";
import {PrecheckCapacityData} from "@generated/precheck-capacity-data";
import {PrecheckEntityType} from "@generated/precheck-entity-type";
import {PrecheckReport} from "@generated/precheck-report";
import {PrecheckTaskData} from "@generated/precheck-task-data";

@Injectable()
export class PrecheckReportService {

   private static readonly PERCENTAGE_MULTIPLIER: number = 100;

   private static readonly EMPTY_SPACE: string = " ";

   getPostOperationCapacityLabel(postOperationCapacity: PrecheckCapacityData, isThinBar: boolean,
         selectedEntityType: PrecheckEntityType): string {
      if (selectedEntityType === PrecheckEntityType.HOST) {
         return VsanUiUtils.getString(
               "vsan.evacuationStatus.report.predictedOverview.capacityTab.table.maintenanceMode");
      }

      return this.getCapacityLabel(postOperationCapacity, isThinBar, true, selectedEntityType);
   }

   getCapacityLabel(capacity: PrecheckCapacityData, isThinBar: boolean, hasCapacityDifference: boolean,
         selectedEntityType?: PrecheckEntityType): string {
      if (!capacity.usedCapacity && !capacity.totalCapacity) {
         return VsanUiUtils.getString("vsan.evacuationStatus.report.predictedOverview.capacityTab.noCapacity");
      }

      let messageKey: string;
      if (isThinBar) {
         if (hasCapacityDifference) {
            const isChildEntitySelected: boolean = selectedEntityType === PrecheckEntityType.DISK_GROUP
                  || selectedEntityType === PrecheckEntityType.DISK;
            messageKey = isChildEntitySelected
                  ? "vsan.evacuationStatus.report.predictedOverview.capacityTab.tableCapacityInfoReducedCapacity"
                  : "vsan.evacuationStatus.report.predictedOverview.capacityTab.tableCapacityInfo";
         } else {
            messageKey = "vsan.evacuationStatus.report.predictedOverview.capacityTab.tableCapacityInfoNoChange";
         }
      } else {
         messageKey = "vsan.evacuationStatus.report.predictedOverview.capacityTab.generalCapacityInfo";
      }

      return VsanUiUtils.getString(messageKey,
            VsanUiUtils.formatFileSize(capacity.usedCapacity),
            VsanUiUtils.formatFileSize(capacity.totalCapacity),
            VsanUiUtils.getPercentage(this.getBarPercentage(capacity.usedCapacity, capacity.totalCapacity)));
   }

   hasCapacityDifference(preOperationalCapacity: PrecheckCapacityData,
         postOperationCapacity: PrecheckCapacityData): boolean {
      return VsanUiUtils.formatFileSize(preOperationalCapacity.usedCapacity) !==
            VsanUiUtils.formatFileSize(postOperationCapacity.usedCapacity)
            || VsanUiUtils.formatFileSize(preOperationalCapacity.totalCapacity) !==
            VsanUiUtils.formatFileSize(postOperationCapacity.totalCapacity);
   }

   getBarChartWidth(beforeTotalCapacity: number, afterTotalCapacity: number): number {
      return (afterTotalCapacity / beforeTotalCapacity) * PrecheckReportService.PERCENTAGE_MULTIPLIER;
   }

   getBarPercentage(usedCapacity: number, totalCapacity: number): number {
      return (usedCapacity / totalCapacity) * PrecheckReportService.PERCENTAGE_MULTIPLIER;
   }

   // decommission mode helper functions
   getSummaryMessage(status: PrecheckResultStatusType, precheckEntity: PrecheckEntityType, dataToMove: number,
         persistenceDataToRebuild?: number, isVsanMaxEnabled?: boolean): string {
      let message: string;
      const hasError: boolean = PrecheckResultStatusType.RED === status;
      switch (precheckEntity) {
         case PrecheckEntityType.HOST:
            if (hasError) {
               return VsanUiUtils.getString("vsan.evacuationStatus.report.preCheckFailed.host");
            }

            message = VsanUiUtils.getString("vsan.evacuationStatus.report.preCheckSucceeded.host");
            break;
         case PrecheckEntityType.DISK_GROUP:
            if (hasError) {
               return VsanUiUtils.getString("vsan.evacuationStatus.report.preCheckFailed.diskGroup");
            }

            message = VsanUiUtils.getString("vsan.evacuationStatus.report.preCheckSucceeded.diskGroup");
            break;
         case PrecheckEntityType.DISK:
            if (hasError) {
               return isVsanMaxEnabled
                     ? VsanUiUtils.getString("vsan.evacuationStatus.report.preCheckFailed.disk.vsanMax")
                     : VsanUiUtils.getString("vsan.evacuationStatus.report.preCheckFailed.disk");
            }

            message = isVsanMaxEnabled
                  ? VsanUiUtils.getString("vsan.evacuationStatus.report.preCheckSucceeded.disk.vsanMax")
                  : VsanUiUtils.getString("vsan.evacuationStatus.report.preCheckSucceeded.disk");
            break;
      }

      if (dataToMove) {
         message = message.concat(PrecheckReportService.EMPTY_SPACE,
               VsanUiUtils.getString("vsan.evacuationStatus.report.dataToMoveMessage",
                     VsanUiUtils.formatFileSize(dataToMove)));
      }

      if (persistenceDataToRebuild) {
         message = message.concat(PrecheckReportService.EMPTY_SPACE,
               VsanUiUtils.getString("vsan.evacuationStatus.report.persistenceDataToRebuildMessage",
                     VsanUiUtils.formatFileSize(persistenceDataToRebuild)));
      }

      return message.trim();
   }

   getObjectsMessage = (inaccessibleObjectsCount: number,
         nonCompliantObjectsCount: number,
         noDurabilityObjectsCount: number): string => {
      const hasInaccessibleObjects: boolean = inaccessibleObjectsCount > 0;
      const hasNonCompliantObjects: boolean = nonCompliantObjectsCount > 0;

      if (!hasInaccessibleObjects && !hasNonCompliantObjects) {
         return VsanUiUtils.getString("vsan.evacuationStatus.report.accessibleAndCompliantObjectsMessage");
      }

      const messages: string[] = [];
      if (hasInaccessibleObjects) {
         messages.push(VsanUiUtils.getString(
               "vsan.evacuationStatus.report.inaccessibleObjectsMessage", inaccessibleObjectsCount));
      }

      if (hasNonCompliantObjects) {
         if (noDurabilityObjectsCount > 0) {
            messages.push(VsanUiUtils.getString(
                  "vsan.evacuationStatus.report.nonCompliantWithoutDurabilityObjectsMessage",
                  nonCompliantObjectsCount, noDurabilityObjectsCount));
         } else {
            messages.push(VsanUiUtils.getString(
                  "vsan.evacuationStatus.report.nonCompliantObjectsMessage", nonCompliantObjectsCount));
         }
      }

      return messages.join(PrecheckReportService.EMPTY_SPACE);
   };

   parseReportStatusToIcon(report: PrecheckReport): string {
      switch (report.status) {
         case PrecheckResultStatusType.GREEN:
            return Icon.CLR_SUCCESS_STANDARD_ICON_SHAPE;
         case PrecheckResultStatusType.YELLOW:
            return Icon.CLR_WARN_STANDARD_ICON_SHAPE;
         case PrecheckResultStatusType.RED:
            return Icon.CLR_ERROR_STANDARD_ICON_SHAPE;
      }
   }

   getVirtualObjectsTabIcon(report: PrecheckReport): string {
      if (report.inaccessibleObjects && report.inaccessibleObjects.length) {
         return Icon.CLR_ERROR_STANDARD_ICON_SHAPE;
      }

      if (report.nonCompliantObjects && report.nonCompliantObjects.length) {
         return Icon.CLR_WARN_STANDARD_ICON_SHAPE;
      }

      return "";
   }

   getCapacityTabIcon(clusterCapacity: PrecheckClusterCapacityData): string {
      const faultDomainErrors: boolean = clusterCapacity.faultDomains.some(
            faultDomain => faultDomain.isAdditionalHostNeeded || faultDomain.hasInsufficientSpace
                  || faultDomain.isComponentLimitReached);
      const standaloneHostErrors: boolean = clusterCapacity.standaloneHosts.some(
            host => !!host.capacityNeeded || host.isComponentLimitReached);

      if (clusterCapacity.faultDomainsNeeded || faultDomainErrors || standaloneHostErrors) {
         return Icon.CLR_ERROR_STANDARD_ICON_SHAPE;
      }

      const initialPercentage: number = this.getBarPercentage(clusterCapacity.preOperationCapacity.usedCapacity,
            clusterCapacity.preOperationCapacity.totalCapacity);
      const finalPercentage: number = this.getBarPercentage(clusterCapacity.postOperationCapacity.usedCapacity,
            clusterCapacity.postOperationCapacity.totalCapacity);

      if (initialPercentage >= clusterCapacity.warningThreshold
            || finalPercentage >= clusterCapacity.warningThreshold) {
         return Icon.CLR_WARN_STANDARD_ICON_SHAPE;
      }

      return "";
   }

   getHealthTabIcon(healthData: VsanHealthData): string {
      switch (healthData.status) {
         case VsanHealthStatus.green:
         case VsanHealthStatus.info:
            return "";
         case VsanHealthStatus.red:
            return Icon.CLR_ERROR_STANDARD_ICON_SHAPE;
         default:
            return Icon.CLR_WARN_STANDARD_ICON_SHAPE;
      }
   }

   getPersistenceTabIcon(persistenceData: PrecheckPersistentInstanceData[]): string {
      if (VsanUiUtils.isEmpty(persistenceData)) {
         return  "";
      } else if (persistenceData.find(p => p.state === PrecheckPersistentInstanceState.INACCESSIBLE)) {
         return Icon.CLR_ERROR_STANDARD_ICON_SHAPE;
      } else if (persistenceData.find(p => p.state === PrecheckPersistentInstanceState.REDUCED_AVAILABILITY)
            || persistenceData.find(p => p.state === PrecheckPersistentInstanceState.REBUILD)) {
         return Icon.CLR_WARN_STANDARD_ICON_SHAPE;
      }

      return "";
   }

   getPersistenceStateLabel(state: PrecheckPersistentInstanceState): string {
      switch (state) {
         case PrecheckPersistentInstanceState.INACCESSIBLE:
            return VsanUiUtils.getString(
                  "vsan.evacuationStatus.report.predictedOverview.persistenceTab.table.predictedState.inaccessible");
         case PrecheckPersistentInstanceState.REDUCED_AVAILABILITY:
            return VsanUiUtils.getString(
                  "vsan.evacuationStatus.report.predictedOverview.persistenceTab.table.predictedState.reducedAvailability");
         case PrecheckPersistentInstanceState.REBUILD:
            return VsanUiUtils.getString(
                  "vsan.evacuationStatus.report.predictedOverview.persistenceTab.table.predictedState.rebuild");
         default: return "";
      }
   }

   getPersistenceStateIcon(state: PrecheckPersistentInstanceState): string {
      switch (state) {
         case PrecheckPersistentInstanceState.INACCESSIBLE:
            return Icon.CLR_ERROR_STANDARD_ICON_SHAPE;
         case PrecheckPersistentInstanceState.REDUCED_AVAILABILITY:
         case PrecheckPersistentInstanceState.REBUILD:
            return Icon.CLR_WARN_STANDARD_ICON_SHAPE;
         default: return "";
      }
   }

   getPersistenceIconClass(state: PrecheckPersistentInstanceState): string {
      const iconShape: string = this.getPersistenceStateIcon(state);
      return this.getIconClasses(iconShape);
   }

   getIconClasses(shape: string): string {
      return `is-solid ${Icon.clrIconShapeClassMap[shape]}`;
   }

   getRunningTaskMessage(runningTask: PrecheckTaskData, isDiskResourcePrecheckSupported: boolean,
         isVsanMaxEnabled: boolean): string {
      if (!runningTask) {
         return "";
      }
      return isDiskResourcePrecheckSupported ? this.getRunningTaskMessageForAllObjects(runningTask, isVsanMaxEnabled)
            : this.getRunningTaskMessageForHost(runningTask);
   }

   private getRunningTaskMessageForHost(runningTask: PrecheckTaskData): string {
      switch (runningTask.taskType) {
         case PrecheckTaskType.HOST_PRECHECK:
            return runningTask.hostName
                  ? VsanUiUtils.getString("vsan.evacuationStatus.report.taskInProgress.precheck", runningTask.hostName,
                        VsanDataMigrationUtil.getLocalizedDecommissionMode(runningTask.decommissionMode))
                  : VsanUiUtils.getString("vsan.evacuationStatus.report.taskInProgress.precheck.unknownHost",
                        VsanDataMigrationUtil.getLocalizedDecommissionMode(runningTask.decommissionMode));
         case PrecheckTaskType.HOST_ENTER_MAINTENANCE_MODE:
            return runningTask.hostName
                  ? VsanUiUtils.getString("vsan.evacuationStatus.report.taskInProgress.maintenanceMode",
                        runningTask.hostName)
                  :  VsanUiUtils.getString("vsan.evacuationStatus.report.taskInProgress.maintenanceMode.unknownHost");
      }
   }

   private getRunningTaskMessageForAllObjects(runningTask: PrecheckTaskData, isVsanMaxEnabled: boolean): string {
      const decommissionMode: string = VsanDataMigrationUtil.getLocalizedDecommissionMode(runningTask.decommissionMode);
      const commonMessage: string = isVsanMaxEnabled
            ? VsanUiUtils.getString("vsan.evacuationStatus.report.taskInProgress.commonMessage.vSanMax")
            : VsanUiUtils.getString("vsan.evacuationStatus.report.taskInProgress.commonMessage");

      switch (runningTask.taskType) {
         case PrecheckTaskType.HOST_PRECHECK:
            return runningTask.hostName
                  ? VsanUiUtils.getString("vsan.evacuationStatus.report.taskInProgress.host.precheck",
                        runningTask.hostName, decommissionMode, commonMessage)
                  : VsanUiUtils.getString("vsan.evacuationStatus.report.taskInProgress.host.precheck.unknownHost",
                        decommissionMode, commonMessage);
         case PrecheckTaskType.HOST_ENTER_MAINTENANCE_MODE:
            return runningTask.hostName
                  ? VsanUiUtils.getString("vsan.evacuationStatus.report.taskInProgress.host.maintenanceMode",
                        runningTask.hostName, commonMessage) :
                  VsanUiUtils.getString("vsan.evacuationStatus.report.taskInProgress.host.maintenanceMode.unknownHost",
                        commonMessage);
         case PrecheckTaskType.DISKGROUP_REMOVAL:
            return VsanUiUtils.getString("vsan.evacuationStatus.report.taskInProgress.removeGroup",
                  decommissionMode, commonMessage);
         case PrecheckTaskType.DISKGROUP_RECREATE:
            return VsanUiUtils.getString("vsan.evacuationStatus.report.taskInProgress.recreateGroup",
                  decommissionMode, commonMessage);
         case PrecheckTaskType.DISKGROUP_UNMOUNT:
            return VsanUiUtils.getString("vsan.evacuationStatus.report.taskInProgress.unmountGroup",
                  decommissionMode, commonMessage);
         case PrecheckTaskType.DISK_REMOVAL:
            return VsanUiUtils.getString("vsan.evacuationStatus.report.taskInProgress.removeDisks",
                  decommissionMode, commonMessage);
         case PrecheckTaskType.DISK_UNMOUNT:
            return VsanUiUtils.getString("vsan.evacuationStatus.report.taskInProgress.unmountDisk",
                  decommissionMode, commonMessage);
         case PrecheckTaskType.DISKGROUP_PRECHECK:
         case PrecheckTaskType.DISK_PRECHECK:
            return VsanUiUtils.getString("vsan.evacuationStatus.report.taskInProgress.diskOrGroup.precheck",
                  runningTask.name, runningTask.hostName, decommissionMode, commonMessage);
         default:
            // Handle the rare case when UI doesn't know host information and if the entity is a disk or disk group.
            // Additional info in https://bugzilla.eng.vmware.com/show_bug.cgi?id=2391487
            return VsanUiUtils.getString("vsan.evacuationStatus.report.taskInProgress.unknownEntityType.unknownHost",
                  commonMessage);
      }
   }

   isTaskForSelectedEntity(runningTask: PrecheckTaskData, selectedEntityUuid: string,
         selectedDecommissionMode: DecommissionMode): boolean {
      return selectedEntityUuid === runningTask.uuid && selectedDecommissionMode === runningTask.decommissionMode;
   }

   getFailedTaskMessage(taskType: PrecheckTaskType, entityName: string): string {
      switch (taskType) {
         case PrecheckTaskType.HOST_ENTER_MAINTENANCE_MODE:
            return VsanUiUtils.getString("vsan.evacuationStatus.report.eMMTaskFailed", entityName);
         case PrecheckTaskType.DISKGROUP_REMOVAL:
            return VsanUiUtils.getString("vsan.evacuationStatus.report.removeGroupFailed", entityName);
         case PrecheckTaskType.DISKGROUP_RECREATE:
            return VsanUiUtils.getString("vsan.evacuationStatus.report.recreateGroupFailed", entityName);
         case PrecheckTaskType.DISKGROUP_UNMOUNT:
            return VsanUiUtils.getString("vsan.evacuationStatus.report.unmountGroupFailed", entityName);
         case PrecheckTaskType.DISK_REMOVAL:
            return VsanUiUtils.getString("vsan.evacuationStatus.report.removeDiskFailed", entityName);
         case PrecheckTaskType.DISK_UNMOUNT:
            return VsanUiUtils.getString("vsan.evacuationStatus.report.unmountDiskFailed", entityName);
         default:
            // Failed to run data migration precheck for host, disk group or disk.
            return VsanUiUtils.getString("vsan.evacuationStatus.report.taskFailed", entityName);
      }
   }

   getClusterLevelCapacityErrorMessage(clusterCapacity: PrecheckClusterCapacityData): string {
      const messages: string[] = [
         VsanUiUtils.getString("vsan.evacuationStatus.report.predictedOverview.capacityTab.requiredStandaloneHost")];

      const hasComponentLimitError: boolean =
            clusterCapacity.faultDomains.some(faultDomain => faultDomain.isComponentLimitReached)
            || clusterCapacity.standaloneHosts.some(standAloneHost => standAloneHost.isComponentLimitReached);

      if (hasComponentLimitError) {
         messages.push(VsanUiUtils.getString(
               "vsan.evacuationStatus.report.predictedOverview.capacityTab.table.componentLimitReached"));
      } else {
         messages.push(VsanUiUtils.getString(
               "vsan.evacuationStatus.report.predictedOverview.capacityTab.storagePolicyIncompliance"));
      }

      return messages.join(" ");
   }

}
