import {Component, EventEmitter, Input, Output} from "@angular/core";
import {PrecheckPersistentInstanceData} from "@generated/precheck-persistent-instance-data";
import {PrecheckPersistentInstanceState} from "@generated/precheck-persistent-instance-state";
import {VsanUiUtils} from "@util/vsan-util";
import {PrecheckReportService} from "../precheck-report.service";

@Component({
   templateUrl: "precheck-persistence-tab.html",
   styleUrls: ["precheck-persistence-tab.scss"],
   selector: "vsan-precheck-persistence-tab",
})
export class PrecheckPersistenceTabComponent {
   @Input()
   persistentInstances: PrecheckPersistentInstanceData[];

   @Output()
   filterChanged: EventEmitter<boolean> = new EventEmitter<boolean>();

   stateFilterValue: PrecheckPersistentInstanceData[];

   readonly DEFAULT_ITEMS_PER_PAGE: number = 10;

   constructor(private precheckReportService: PrecheckReportService) {
   }

   getPersistenceStateLabel = (state: PrecheckPersistentInstanceState): string => {
      return this.precheckReportService.getPersistenceStateLabel(state);
   };

   getPersistenceStateIcon = (state: PrecheckPersistentInstanceState): string => {
      return this.precheckReportService.getPersistenceStateIcon(state);
   };

   getPersistenceStateIconClasses = (state: PrecheckPersistentInstanceState): string => {
      return this.precheckReportService.getPersistenceIconClass(state);
   };

   onSelectedFilterTypeChanged(value: PrecheckPersistentInstanceData[]): void {
      this.stateFilterValue = value;
      this.notifyFilterChange();
   }

   notifyFilterChange(): void {
      const hasAppliedFilters: boolean = !VsanUiUtils.isEmpty(this.stateFilterValue);
      this.filterChanged.emit(hasAppliedFilters);
   }
}
