import {Component, OnInit} from "@angular/core";
import {environment} from "@env/environment";
import {DecommissionMode} from "@generated/decommission-mode";
import {PrecheckService} from "@generated/precheck-service";
import {ClusterComputeResource, VirtualMachine} from "@service/client/dataservice/data-service-property";
import {DataServiceQueryResult} from "@service/client/dataservice/data-service.data";
import {DataServiceClient} from "@service/client/dataservice/data-service.service";
import {ManagedObject} from "@service/managed-object";
import {VsanDataMigrationUtil} from "@util/vsan-data-migration-util";
import {VsanUiUtils} from "@util/vsan-util";
import {EnterMaintenanceModeDialogContext} from "./enter-maintenance-mode-dialog-context";

@Component({
   templateUrl: "enter-maintenance-mode-dialog.html",
   styleUrls: ["enter-maintenance-mode-dialog.scss"],
})
export class EnterMaintenanceModeDialogComponent implements OnInit {

   // DataService properties
   private static readonly CLUSTER_DRS_ENABLED_PROPERTY: string = "configurationEx.drsConfig.enabled";
   private static readonly HOST_POWERED_ON_VMS_PROPERTY: string = "poweredOnVMs";

   // Dialog size
   static readonly EMM_DIALOG_WIDTH: number = 700;
   static readonly EMM_DIALOG_HEIGHT: number = 320;
   private static readonly HEIGHT_ADDITION_POWERED_ON_VMS: number = 70;
   private static readonly HEIGHT_ADDITION_DRS: number = 50;
   private static readonly HEIGHT_ADDITION_DURABILITY: number = 100;

   // todo georgig: add KB article id when provided. Bug 2676152
   readonly ENHANCED_DURABILITY_KB: string = "";

   decommissionModeLocalized: string;
   secondaryTitle: string;
   hostRef: ManagedObject;
   alertMessages: string[];
   isDrsEnabled: boolean;
   movePoweredOffVms: boolean;
   hasPoweredOnVms: boolean;
   loading: boolean;
   isDurabilityPossible: boolean;
   isDurabilityGuaranteed: boolean;
   ensureDurability: boolean;

   private decommissionMode: DecommissionMode;

   constructor(private dataService: DataServiceClient,
         private precheckService: PrecheckService) {
   }

   ngOnInit(): void {
      this.loading = true;

      const context: EnterMaintenanceModeDialogContext = VsanUiUtils.getModalContext();
      this.secondaryTitle = context.precheckEntity.name;
      this.decommissionMode = context.decommissionMode;
      this.decommissionModeLocalized = VsanDataMigrationUtil.getLocalizedDecommissionMode(this.decommissionMode);
      this.hostRef = context.precheckEntity.hostRef;
      this.isDurabilityPossible = context.isDurabilityPossible;
      this.isDurabilityGuaranteed = context.isDurabilityGuaranteed;

      Promise
            .all([
               this.dataService.getProperty(ClusterComputeResource.configurationEx.drsConfig.enabled, context.clusterRef),
               this.getNumberOfPowerOnVmsOnHost(),
            ])
            .then(this.onPropertiesRetrieved)
            .catch(this.onError);
   }

   private async getNumberOfPowerOnVmsOnHost(): Promise<number> {
      if (environment.repa) {
         // The original "poweredOnVMs" property is provided by H5C so we don't have access to it
         // through the vsperejs/APIGW interface. Hence we need to craft the query by ourselves.
         const result: DataServiceQueryResult = await this.dataService
               .select()
               .fromType(VirtualMachine.type)
               .where()
               .propertyEqualsMoRef(VirtualMachine.runtime.host, this.hostRef)
               .and()
               .propertyEquals(VirtualMachine.runtime.powerState, "poweredOn")
               .execute();
         return result.resultItems.length;
      } else {
         // In LOCAL mode fallback to the Java DS layer
         const result: any[] = await this.dataService
               .getProperty(EnterMaintenanceModeDialogComponent.HOST_POWERED_ON_VMS_PROPERTY, this.hostRef);
         return !result ? 0 : result.length;
      }
   }

   async onConfirm(): Promise<void> {
      this.loading = true;
      // pass EMM task to parent component
      let precheckTask: ManagedObject;
      try {
         if (this.isDurabilityGuaranteed && this.ensureDurability) {
            this.decommissionMode = DecommissionMode.ensureEnhancedDurability;
         }
         precheckTask = await this.precheckService.runEnterMaintenanceMode(this.hostRef,
               this.decommissionMode.toString(), this.movePoweredOffVms);
      } catch (error) {
         this.onError(error);
      }

      VsanUiUtils.closeModalDialog(precheckTask);
   }

   getDurabilityAlertType(): string {
      if (!this.isDurabilityPossible) {
         return;
      }
      return this.isDurabilityGuaranteed ? "info" : "warning";
   }

   getDurabilityAlertMessage(): string {
      if (!this.isDurabilityPossible) {
         return;
      }
      return this.isDurabilityGuaranteed ?
            VsanUiUtils.getString("vsan.evacuationStatus.report.enterMaintenanceModeDialog.ensureDurabilityInfo") :
            VsanUiUtils.getString("vsan.evacuationStatus.report.enterMaintenanceModeDialog.ensureDurabilityWarn");
   }

   private onPropertiesRetrieved = (results: any[]) => {
      this.isDrsEnabled = !!results[0];
      if (this.isDrsEnabled) {
         // when DRS is enabled, the option to move powered off VMs is enabled by default
         this.movePoweredOffVms = true;
      }

      this.hasPoweredOnVms = !!results[1];

      // enlarge dialog according to the properties shown
      let height: number = EnterMaintenanceModeDialogComponent.EMM_DIALOG_HEIGHT;
      if (this.isDrsEnabled) {
         height += EnterMaintenanceModeDialogComponent.HEIGHT_ADDITION_DRS;
      }
      if (this.hasPoweredOnVms) {
         height += EnterMaintenanceModeDialogComponent.HEIGHT_ADDITION_POWERED_ON_VMS;
      }
      if (this.isDurabilityPossible) {
         height += EnterMaintenanceModeDialogComponent.HEIGHT_ADDITION_DURABILITY;
      }
      VsanUiUtils.setModalDialogHeight(height);

      this.loading = false;
   };

   private onError = (error: any) => {
      this.alertMessages = [error];
      this.loading = false;
   };
}
