import {Component, Input} from "@angular/core";
import {BarChartService} from "@component/chart/capacity-bar-chart/bar-chart.service";
import {DatastoreType} from "@generated/datastore-type";
import {PrecheckClusterCapacityData} from "@generated/precheck-cluster-capacity-data";
import {PrecheckEntityType} from "@generated/precheck-entity-type";
import {PrecheckFaultDomainCapacityData} from "@generated/precheck-fault-domain-capacity-data";
import {PrecheckHostCapacityData} from "@generated/precheck-host-capacity-data";
import {Icon} from "@util/icon";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "precheck-capacity-breakdown.html",
   styleUrls: ["precheck-capacity-tab.scss"],
   selector: "vsan-precheck-capacity-breakdown",
})
export class PrecheckCapacityBreakdownComponent {

   readonly Icon: typeof Icon = Icon;
   readonly PrecheckEntityType: typeof PrecheckEntityType = PrecheckEntityType;

   @Input()
   clusterCapacity: PrecheckClusterCapacityData;

   @Input()
   hasError: boolean = false;

   @Input()
   datastoreType: DatastoreType = DatastoreType.VSAN;

   constructor(private barChartService: BarChartService) {
   }

   getGreenColor = (): string => {
      return this.barChartService.getGreenPallete()[0];
   };

   getYellowColor = (): string => {
      return this.barChartService.getYellowPallete()[0];
   };

   getRedColor = (): string => {
      return this.barChartService.getRedPallete()[0];
   };

   getCapacityColumnHeader = (): string => {
      return this.datastoreType === DatastoreType.VSAN_DIRECT
            ? VsanUiUtils.getString(
                  "vsan.evacuationStatus.report.predictedOverview.capacityTab.table.capacityHeader.vsanDirect")
            : VsanUiUtils.getString("vsan.evacuationStatus.report.predictedOverview.capacityTab.table.capacityHeader");
   };

   getInsufficientSpaceLabel = (insufficientSpace: number): string => {
      return VsanUiUtils.getString(
            "vsan.evacuationStatus.report.predictedOverview.capacityTab.table.insufficientSpaceOnHost",
            VsanUiUtils.formatFileSize(insufficientSpace));
   };

   showCapacityTable = (): boolean => {
      return !this.clusterCapacity.faultDomainsNeeded && this.hasEntities();
   };

   hasFaultDomainErrors = (faultDomainCapacityData: PrecheckFaultDomainCapacityData): boolean => {
      return faultDomainCapacityData.hasInsufficientSpace || faultDomainCapacityData.isAdditionalHostNeeded
            || faultDomainCapacityData.isComponentLimitReached;
   };

   hasHostErrors = (hostsCapacityData: PrecheckHostCapacityData): boolean => {
      return (this.hasError && !!hostsCapacityData.selectedEntityType)
            || !!hostsCapacityData.capacityNeeded || hostsCapacityData.isComponentLimitReached;
   };

   private hasEntities(): boolean {
      const hasFaultDomains: boolean = !!(this.clusterCapacity.faultDomains
            && this.clusterCapacity.faultDomains.length);
      const hasStandaloneHosts: boolean = !!(this.clusterCapacity.standaloneHosts
            && this.clusterCapacity.standaloneHosts.length);

      return hasFaultDomains || hasStandaloneHosts;
   }
}
