import {Component, EventEmitter, HostBinding, Input, OnInit, Output} from "@angular/core";
import {GuardRailMessageStatus} from "@generated/guard-rail-message-status";
import {GuardRailPropertyProviderAdapter} from "@generated/guard-rail-property-provider-adapter";
import {PrecheckReport} from "@generated/precheck-report";
import {PrecheckService} from "@generated/precheck-service";
import {RefreshHandler} from "@service/global-refresh.service";
import {ManagedObject} from "@service/managed-object";
import {Logger} from "@util/logger";
import {ReferenceWatcher} from "@util/reference-watcher";
import {PrecheckReportService} from "../report/precheck-report.service";

@Component({
   templateUrl: "precheck-guard-rails.html",
   styleUrls: ["precheck-guard-rails.scss"],
   selector: "vsan-precheck-guard-rails",
   providers: [ReferenceWatcher],
})
export class PrecheckGuardRailsComponent implements OnInit {
   @Input()
   isDiskResourcePrecheckSupported: boolean;

   @Input()
   isVsanMaxEnabled: boolean;

   @Output()
   hasRunningTask: EventEmitter<boolean> = new EventEmitter<boolean>();

   @HostBinding("class")
   get noContentDisplayed(): string {
      return (this.warningMessages && this.warningMessages.length > 0) || this.runningTaskMessage || this.alertMessage ?
            "" : PrecheckGuardRailsComponent.NO_BOTTOM_SPACING_CLASS;
   }

   private static readonly GUARD_RAIL_MESSAGES_PROPERTY: string = "clusterGuardRailMessages";
   private static readonly NO_BOTTOM_SPACING_CLASS: string = "no-bottom-spacing";
   private static readonly TIMEOUT_IN_MS: number = 5000;

   warningMessages: string[] = [];
   alertMessage: string;
   runningTaskMessage: string;

   constructor(private precheckService: PrecheckService,
         private precheckReportService: PrecheckReportService,
         private guardRailService: GuardRailPropertyProviderAdapter,
         private refWatcher: ReferenceWatcher) {
   }

   async ngOnInit(): Promise<any> {
      await this.fetch();
      this.refWatcher.setInterval(() => this.fetch(), PrecheckGuardRailsComponent.TIMEOUT_IN_MS);
   }

   @RefreshHandler
   private async fetch(): Promise<any> {
      try {
         const result: any[] = await Promise.all([
            this.precheckService.getPrecheckReport(ManagedObject.contextObject, null, null, null),
            this.guardRailService.getGuardRailsMessages(ManagedObject.contextObject),
         ]);
         if (result && result.length === 2) {
            this.populateRunningTaskMessage(result[0]);
            this.populateGuardRailMessages(result[1]);
         }
      } catch (error) {
         Logger.error(`Unexpected error ${error} encountered while querying Cluster guard rail messages`);
         this.warningMessages = [];
         this.runningTaskMessage = undefined;
         this.alertMessage = error;
      }
   }

   private populateRunningTaskMessage = (precheckReport: PrecheckReport) => {
      if (!precheckReport || !precheckReport.runningTask) {
         this.hasRunningTask.emit(false);
         this.runningTaskMessage = undefined;
         return;
      }

      this.hasRunningTask.emit(true);
      this.runningTaskMessage = this.precheckReportService.getRunningTaskMessage(
            precheckReport.runningTask, this.isDiskResourcePrecheckSupported, this.isVsanMaxEnabled);
   };

   private populateGuardRailMessages = (guardRailMessages: Map<GuardRailMessageStatus, string[]>) => {
      this.warningMessages = guardRailMessages[GuardRailMessageStatus.WARNING];
   };
}
