import {ChangeDetectionStrategy, Component, Input, OnInit} from "@angular/core";
import {BarChartService} from "@component/chart/capacity-bar-chart/bar-chart.service";
import {PrecheckCapacityData} from "@generated/precheck-capacity-data";
import {PrecheckEntityType} from "@generated/precheck-entity-type";
import {PrecheckReportService} from "../precheck-report.service";

@Component({
   templateUrl: "precheck-capacity-bar-chart.html",
   styleUrls: ["precheck-capacity-bar-chart.scss"],
   selector: "vsan-precheck-capacity-bar-chart",
   changeDetection: ChangeDetectionStrategy.OnPush,
})
export class PrecheckCapacityBarChartComponent implements OnInit {
   @Input()
   preOperationCapacity: PrecheckCapacityData;

   @Input()
   postOperationCapacity: PrecheckCapacityData;

   @Input()
   warningThreshold: number;

   @Input()
   errorThreshold: number;

   @Input()
   isThinBar: boolean;

   @Input()
   selectedEntityType: PrecheckEntityType;

   afterBarWidth: number;

   beforeUsedCapacityWidth: number;

   afterUsedCapacityWidth: number;

   beforeBarColor: string;

   afterBarColor: string;

   constructor(private precheckReportService: PrecheckReportService,
         private barChartService: BarChartService) {
   }

   ngOnInit(): void {
      this.afterBarWidth = this.precheckReportService.getBarChartWidth(this.preOperationCapacity.totalCapacity,
            this.postOperationCapacity.totalCapacity) || 100;

      this.beforeBarColor = this.barChartService.getColorPalette(this.preOperationCapacity.usedCapacity,
            this.preOperationCapacity.totalCapacity, this.warningThreshold, this.errorThreshold)[0];
      this.afterBarColor = this.barChartService.getColorPalette(this.postOperationCapacity.usedCapacity,
            this.postOperationCapacity.totalCapacity, this.warningThreshold, this.errorThreshold)[0];

      this.beforeUsedCapacityWidth = this.barChartService.asPercentage(this.preOperationCapacity.usedCapacity,
            this.preOperationCapacity.totalCapacity);
      this.afterUsedCapacityWidth = this.barChartService.asPercentage(this.postOperationCapacity.usedCapacity,
            this.postOperationCapacity.totalCapacity);
   }

   hasBeforeBar(): boolean {
      return !this.selectedEntityType || !!this.preOperationCapacity.totalCapacity;
   }

   // display the after bar when either there is capacity difference, cluster level bars are displayed,
   // or the bar is for the selected entity
   hasAfterBar(): boolean {
      return this.hasCapacityDifference() || !this.isThinBar || !!this.selectedEntityType;
   }

   getPreOperationCapacityLabel(): string {
      return this.precheckReportService.getCapacityLabel(this.preOperationCapacity, this.isThinBar,
            this.hasCapacityDifference());
   }

   getPostOperationCapacityLabel(): string {
      return this.precheckReportService.getPostOperationCapacityLabel(this.postOperationCapacity, this.isThinBar,
            this.selectedEntityType);
   }

   hasCapacityDifference(): boolean {
      return this.precheckReportService.hasCapacityDifference(this.preOperationCapacity,
            this.postOperationCapacity);
   }
}
