import {Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {DecommissionMode} from "@generated/decommission-mode";
import {DurabilityState} from "@generated/durability-state";
import {PermissionService} from "@generated/permission-service";
import {PrecheckClusterCapacityData} from "@generated/precheck-cluster-capacity-data";
import {PrecheckEntity} from "@generated/precheck-entity";
import {PrecheckEntityType} from "@generated/precheck-entity-type";
import {PrecheckOperation} from "@generated/precheck-operation";
import {PrecheckReport} from "@generated/precheck-report";
import {PrecheckResultStatusType} from "@generated/precheck-result-status-type";
import {PrecheckService} from "@generated/precheck-service";
import {PrecheckTaskData} from "@generated/precheck-task-data";
import {PrecheckTaskType} from "@generated/precheck-task-type";
import {Privileges} from "@generated/privileges";
import {VirtualObjectModel} from "@generated/virtual-object-model";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {VsanConfigService} from "@generated/vsan-config-service";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {WhatIfPropertyProvider} from "@generated/what-if-property-provider";
import {DiskGroupActionData} from "@model/disk-group-action.data";
import {HostActionData} from "@model/host-action.data";
import {DeleteDisksContext} from "@navigation/context/delete-disks-context";
import {DiskGroupActionContext} from "@navigation/context/disk-group-action-context";
import {UnmountDiskContext} from "@navigation/context/unmount-disk-context";
import {ManagedObject} from "@service/managed-object";
import {Icon} from "@util/icon";
import {Logger} from "@util/logger";
import {ModalBuilder} from "@util/modal-builder";
import {VsanDiskMgmtActionsUtil} from "@util/vsan-disk-mgmt-actions-util";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanRoutes} from "../../../vsan-routes";
import {EnterMaintenanceModeDialogContext} from "../enter-maintenance-mode-dialog-context";
import {EnterMaintenanceModeDialogComponent} from "../enter-maintenance-mode-dialog.component";
import {CapacityReport} from "./capacity/precheck-capacity-tab.component";
import {PrecheckReportService} from "./precheck-report.service";

@Component({
   templateUrl: "precheck-report.html",
   styleUrls: ["precheck-report.scss"],
   selector: "vsan-precheck-report",
})
export class PrecheckReportComponent implements OnInit {

   private readonly NO_DURABILITY_STATES: DurabilityState[] = [
      DurabilityState.EXCEEDED_COMP_LIMIT,
      DurabilityState.NO_RESOURCE,
      DurabilityState.NO_SPACE,
      DurabilityState.STALE_DURABILITY_COMP,
   ];

   readonly Icon: Icon = Icon;
   loadingReport: boolean;
   loadingInitialInfo: boolean;
   alertMessage: string;
   report: PrecheckReport;
   durableVirtualObjects: VirtualObjectModel[] = [];
   statusIcon: string;
   virtualObjectsTabStatusIcon: string;
   capacityTabStatusIcon: string;
   healthTabStatusIcon: string;
   persistenceTabStatusIcon: string;
   isCapacityTabActive: boolean;
   isHealthTabActive: boolean;
   isVirtualObjectsTabActive: boolean;
   isPersistenceTabActive: boolean;
   isTaskForSelectedEntity: boolean;

   // Actions status
   enterMaintenanceModeVisible: boolean;
   enterMaintenanceModeDisabledReason: string;
   removeDiskGroupVisible: boolean;
   recreateDiskGroupVisible: boolean;
   unmountDiskGroupVisible: boolean;
   removeDiskVisible: boolean;
   unmountVsanMaxDiskVisible: boolean;
   isDiskAndGroupActionDisabled: boolean;
   diskAndGroupActionDisabledReason: string;

   @Input()
   diskGroupOfSelectedDisk: PrecheckEntity;

   @Output()
   precheckTaskStarted: EventEmitter<PrecheckTaskData> = new EventEmitter<PrecheckTaskData>();

   @Input()
   precheckEntity: PrecheckEntity;

   @Input()
   decommissionMode: DecommissionMode;

   @Input()
   isVsanMaxEnabled: boolean;

   private _reportRefreshed: boolean;

   @Input()
   set reportRefreshed(refreshed: boolean) {
      this._reportRefreshed = refreshed;
      if (!refreshed && this.isInit) {
         this.loadReport();
         this.updateActionsStatus();
      }
   }

   get reportRefreshed(): boolean {
      return this._reportRefreshed;
   }

   @Output()
   refreshReport: EventEmitter<void> = new EventEmitter<void>();

   private isInit: boolean;
   private autoClaimMode: boolean;
   private hasEMMPermissions: boolean;
   private hasEditPermission: boolean;
   private hasStoragePermission: boolean;
   private capabilities: VsanCapabilityData;

   private _hasReportError: boolean;

   set hasReportError(value: boolean) {
      if (this._hasReportError !== value) {
         this._hasReportError = value;
         this.updateActionsStatus();
      }
   }

   get hasReportError(): boolean {
      return this._hasReportError;
   }

   get getCapacityReports(): Map<CapacityReport, PrecheckClusterCapacityData> {
      const capacityReports: Map<CapacityReport, PrecheckClusterCapacityData> =
            new Map<CapacityReport, PrecheckClusterCapacityData>();
      if (this.report.clusterCapacity != null) {
         capacityReports.set(CapacityReport.VSAN, this.report.clusterCapacity);
      }
      if (this.report.vsanDirectClusterCapacity != null) {
         capacityReports.set(CapacityReport.VSAN_DIRECT, this.report.vsanDirectClusterCapacity);
      }

      return capacityReports;
   }

   get hasPersistenceDataToRebuild(): boolean {
      return this.report.persistenceData && this.report.persistenceData.dataToRebuild > 0;
   }

   get isRecreateDiskGroupSupported(): boolean {
      return this.capabilities && this.capabilities.isRecreateDiskGroupSupported;
   }

   constructor(private precheckService: PrecheckService,
         private precheckReportService: PrecheckReportService,
         private whatIfPropertyProvider: WhatIfPropertyProvider,
         private capabilityProvider: VsanCapabilityProvider,
         private permissionService: PermissionService,
         private vsanConfigService: VsanConfigService) {
   }

   async ngOnInit(): Promise<void> {
      const clusterRef: ManagedObject = ManagedObject.contextObject;
      this.loadingInitialInfo = true;

      try {
         let privileges: Map<string, boolean>;
         let vsanConfigSpec: VsanConfigSpec;
         [privileges, this.capabilities, vsanConfigSpec] = await Promise.all([
            this.permissionService.queryPermissions(
                  clusterRef, [
                     Privileges.EDIT_CLUSTER(),
                     Privileges.CONFIG_STORAGE(),
                     Privileges.HOST_MAINTENANCE(),
                  ]),
            this.capabilityProvider.getClusterCapabilityData(clusterRef),
            this.vsanConfigService.getVsanConfigSpec(clusterRef),
         ]);

         this.hasEditPermission = privileges[Privileges.EDIT_CLUSTER()];
         this.hasEMMPermissions = privileges[Privileges.HOST_MAINTENANCE()];
         this.hasStoragePermission = privileges[Privileges.CONFIG_STORAGE()];
         this.autoClaimMode = vsanConfigSpec.autoClaimDisks;
      } catch (error) {
         this.hasEditPermission = true;
         this.hasEMMPermissions = true;
         this.hasStoragePermission = true;
         Logger.error("Unable to query the permissions or basic information required for precheck report operations. "
               + error);
      } finally {
         this.loadingInitialInfo = false;
      }
      if (!this.reportRefreshed) {
         await this.loadReport();
      }
      this.updateActionsStatus();
      this.isInit = true;
   }

   private async loadReport(): Promise<void> {
      this.loadingReport = true;
      try {
         const operation: PrecheckOperation = this.precheckEntity.type === PrecheckEntityType.HOST
               ? PrecheckOperation.ENTER_MAINTENANCE_MODE
               : PrecheckOperation.DISK_DATA_EVACUATION;
         this.report = await this.precheckService.getPrecheckReport(
               ManagedObject.contextObject, this.precheckEntity, this.decommissionMode.toString(), operation);
      } catch (error) {
         this.alertMessage = error;
      }
      this.loadingReport = false;
      this.refreshReport.emit();

      // If the report is null or it doesn't belong to the selected entity and decommission mode, return.
      if (!this.report || this.report.uuid !== this.precheckEntity.uuid ||
            this.report.decommissionMode !== this.decommissionMode.toString()) {
         return;
      }

      if (this.report.runningTask) {
         this.isTaskForSelectedEntity = this.precheckReportService.isTaskForSelectedEntity(this.report.runningTask,
               this.precheckEntity.uuid, this.decommissionMode);
         this.precheckTaskStarted.emit(this.report.runningTask);
         return;
      }

      if (this.report.hasPrecheckReport) {
         this.statusIcon = this.precheckReportService.parseReportStatusToIcon(this.report);
         this.hasReportError = this.report.status === PrecheckResultStatusType.RED;
         this.capacityTabStatusIcon = this.precheckReportService.getCapacityTabIcon(this.report.clusterCapacity);
         // in case of an error we only show capacity related information
         if (!this.hasReportError) {
            this.virtualObjectsTabStatusIcon = this.precheckReportService.getVirtualObjectsTabIcon(this.report);
            this.durableVirtualObjects = this.report.virtualObjects;
            this.healthTabStatusIcon = this.precheckReportService.getHealthTabIcon(this.report.healthSummary);
            this.persistenceTabStatusIcon = this.getPersistenceTabIcon();
         } else {
            this.durableVirtualObjects = [];
         }
      }
   }

   displayDurabilityColumn(): boolean {
      return this.precheckEntity.type === PrecheckEntityType.HOST &&
            this.capabilities.isEnsureDurabilitySupported &&
            this.decommissionMode === DecommissionMode.ensureObjectAccessibility;
   }

   onEnterMaintenanceModeClicked(): void {
      const context: EnterMaintenanceModeDialogContext = {
         decommissionMode: this.decommissionMode,
         precheckEntity: this.precheckEntity,
         clusterRef: ManagedObject.contextObject,
         isDurabilityPossible: this.report.isDurabilityPossible,
         isDurabilityGuaranteed: this.report.isDurabilityGuaranteed,
      };

      new ModalBuilder("evacuationStatus/enterMaintenanceModeDialog")
            .setSize(EnterMaintenanceModeDialogComponent.EMM_DIALOG_WIDTH,
                  EnterMaintenanceModeDialogComponent.EMM_DIALOG_HEIGHT)
            .open(context)
            .then(taskRef => {
               this.handleTask(taskRef, PrecheckTaskType.HOST_ENTER_MAINTENANCE_MODE);
            });
   }

   deleteDiskGroup(): void {
      if (!this.isDiskGroupSelected()) {
         return;
      }
      new ModalBuilder(`${VsanRoutes.DISK_MANAGEMENT_VIEW}/deleteDiskGroup`)
            .setSize(VsanDiskMgmtActionsUtil.DELETE_DISK_GROUP_WIDTH,
                  VsanDiskMgmtActionsUtil.DELETE_DISK_GROUP_HEIGHT_SMALL)
            .open(this.getDiskGroupActionContext())
            .then(taskRef => {
               this.handleTask(taskRef, PrecheckTaskType.DISKGROUP_REMOVAL);
            });
   }

   recreateDiskGroup(): void {
      if (!this.isDiskGroupSelected()) {
         return;
      }
      new ModalBuilder(`${VsanRoutes.DISK_MANAGEMENT_VIEW}/recreateDiskGroup`)
            .setSize(VsanDiskMgmtActionsUtil.RECREATE_DIALOG_WIDTH,
                  VsanDiskMgmtActionsUtil.RECREATE_DIALOG_HEIGHT_SMALL)
            .open(this.getDiskGroupActionContext())
            .then(taskRef => {
               this.handleTask(taskRef, PrecheckTaskType.DISKGROUP_RECREATE);
            });
   }

   unmountDiskGroup(): void {
      if (!this.isDiskGroupSelected()) {
         return;
      }
      new ModalBuilder(`${VsanRoutes.DISK_MANAGEMENT_VIEW}/unmountDiskGroup`)
            .setSize(VsanDiskMgmtActionsUtil.UNMOUNT_DISK_GROUP_DIALOG_WIDTH,
                  VsanDiskMgmtActionsUtil.UNMOUNT_DISK_GROUP_DIALOG_HEIGHT_SMALL)
            .open(this.getDiskGroupActionContext())
            .then(taskRef => {
               this.handleTask(taskRef, PrecheckTaskType.DISKGROUP_UNMOUNT);
            });
   }

   private isDiskGroupSelected(): boolean {
      return this.precheckEntity.type === PrecheckEntityType.DISK_GROUP;
   }

   private getDiskGroupActionContext(): DiskGroupActionContext {
      return {
         decommissionMode: this.decommissionMode,
         host: HostActionData.fromPrecheckEntity(this.precheckEntity),
         diskGroup: DiskGroupActionData.fromPrecheckEntity(this.precheckEntity),
      };
   }

   private handleTask(taskRef: ManagedObject, precheckTaskType: PrecheckTaskType): void {
      const precheckTask: PrecheckTaskData = PrecheckTaskData.Factory.create(
            taskRef, this.precheckEntity.name, this.precheckEntity.uuid,
            null, this.decommissionMode, precheckTaskType);
      this.precheckTaskStarted.emit(precheckTask);
   }

   private getPersistenceTabIcon(): string {
      return this.report.persistenceData
            ? this.precheckReportService.getPersistenceTabIcon(this.report.persistenceData.persistentInstances)
            : "";
   }

   deleteDisk(): void {
      if (this.precheckEntity.type !== PrecheckEntityType.DISK) {
         return;
      }

      const context: DeleteDisksContext = {
         disks: [this.precheckEntity.diskData],
         decommissionMode: this.decommissionMode,
         host: HostActionData.fromPrecheckEntity(this.precheckEntity),
         diskGroups: this.getDiskGroupActionData(),
         isVsanMaxEnabled: this.isVsanMaxEnabled,
      };
      new ModalBuilder(`${VsanRoutes.DISK_MANAGEMENT_VIEW}/deleteDisks`)
            .setSize(VsanDiskMgmtActionsUtil.DELETE_DISKS_WIDTH,
                  VsanDiskMgmtActionsUtil.DELETE_DISKS_HEIGHT_SMALL)
            .open(context)
            .then((taskRefs: ManagedObject[]) => {
               this.handleTask(taskRefs[0], PrecheckTaskType.DISK_REMOVAL);
            });
   }

   unmountVsanMaxDisk(): void {
      if (this.precheckEntity.type !== PrecheckEntityType.DISK) {
         return;
      }

      const context: UnmountDiskContext = {
         disk: this.precheckEntity.diskData,
         decommissionMode: this.decommissionMode,
         host: HostActionData.fromPrecheckEntity(this.precheckEntity),
      };
      new ModalBuilder(`${VsanRoutes.DISK_MANAGEMENT_VIEW}/unmountDisk`)
            .setSize(VsanDiskMgmtActionsUtil.UNMOUNT_DISK_DIALOG_WIDTH,
                  VsanDiskMgmtActionsUtil.UNMOUNT_DISK_DIALOG_HEIGHT_SMALL)
            .open(context)
            .then((task: ManagedObject) => {
               this.handleTask(task, PrecheckTaskType.DISK_UNMOUNT);
            });
   }

   getSummaryReportMessage(): string {
      const persistenceDataToRebuild: number = this.report.persistenceData && this.report.persistenceData.dataToRebuild;
      return this.precheckReportService.getSummaryMessage(this.report.status, this.precheckEntity.type,
            this.report.dataToMove, persistenceDataToRebuild, this.isVsanMaxEnabled);
   }

   private updateActionsStatus(): void {
      this.enterMaintenanceModeVisible = false;
      this.removeDiskGroupVisible = false;
      this.recreateDiskGroupVisible = false;
      this.unmountDiskGroupVisible = false;
      this.removeDiskVisible = false;
      this.unmountVsanMaxDiskVisible = false;
      if (this.hasReportError) {
         return;
      }

      switch (this.precheckEntity.type) {
         case PrecheckEntityType.HOST:
            this.enterMaintenanceModeVisible = true;
            this.enterMaintenanceModeDisabledReason = !this.hasEditPermission || !this.hasEMMPermissions
                  ? VsanUiUtils.getString("vsan.common.nopermissions")
                  : "";
            break;
         case PrecheckEntityType.DISK_GROUP:
            this.removeDiskGroupVisible = !this.autoClaimMode;
            this.recreateDiskGroupVisible = !this.autoClaimMode
                  && this.isRecreateDiskGroupSupported
                  && this.precheckEntity.diskGroupData.isHealthy;
            this.unmountDiskGroupVisible = true;
            break;
         case PrecheckEntityType.DISK:
            this.removeDiskVisible = !this.autoClaimMode;
            this.unmountVsanMaxDiskVisible = this.isVsanMaxEnabled;
            this.isDiskAndGroupActionDisabled = !this.hasEditPermission || !this.hasStoragePermission;
            this.diskAndGroupActionDisabledReason = this.isDiskAndGroupActionDisabled
                  ? VsanUiUtils.getString("vsan.common.nopermissions")
                  : "";
            break;
      }
   }

   getIconClasses(shape: string): string {
      return this.precheckReportService.getIconClasses(shape);
   }

   getInaccessibleObjectsCount(): number {
      return !this.report?.inaccessibleObjects
            ? 0
            : this.report.inaccessibleObjects.length;
   }

   getNonCompliantObjectsCount(): number {
      return !this.report?.nonCompliantObjects
            ? 0
            : this.report.nonCompliantObjects.length;
   }

   getObjectsWithoutEnhancedDurabilityCount(): number {
      return !this.report
            ? 0
            : this.countObjectsWithoutDurability(this.report.virtualObjects);
   }

   private countObjectsWithoutDurability(virtualObjects: VirtualObjectModel[]): number {
      if (!virtualObjects?.length) {
         return 0;
      }
      // 1. get count of virtual objects, for which the durability state is not guaranteed.
      const objectsWithoutDurabilityState: VirtualObjectModel[] = virtualObjects.filter(
            obj => this.NO_DURABILITY_STATES.find(state => state === obj.durabilityState),
      );
      let count: number = objectsWithoutDurabilityState.length;
      // 2. count children of each object, for which the durability state is not guaranteed
      virtualObjects.forEach(obj => count += this.countObjectsWithoutDurability(obj.children));
      return count;
   }

   private getDiskGroupActionData(): DiskGroupActionData[] {
      return this.isVsanMaxEnabled
            ? []
            : [DiskGroupActionData.fromPrecheckEntity(this.diskGroupOfSelectedDisk)];
   }
}
