import {Component, OnInit} from "@angular/core";
import ObjectInfo from "@component/inventory/tree-view/ObjectInfo";
import {TreeNode} from "@component/inventory/tree-view/tree-node";
import {UnavailableViewSpec} from "@component/unavailable-view/unavailable-view-spec";
import {DecommissionMode} from "@generated/decommission-mode";
import {DiskData} from "@generated/disk-data";
import {PermissionService} from "@generated/permission-service";
import {PrecheckData} from "@generated/precheck-data";
import {PrecheckEntity} from "@generated/precheck-entity";
import {PrecheckEntityType} from "@generated/precheck-entity-type";
import {PrecheckOperation} from "@generated/precheck-operation";
import {PrecheckService} from "@generated/precheck-service";
import {PrecheckTaskData} from "@generated/precheck-task-data";
import {PrecheckTaskType} from "@generated/precheck-task-type";
import {Privileges} from "@generated/privileges";
import {SpaceEfficiencyConfig} from "@generated/space-efficiency-config";
import {TaskInfoData} from "@generated/task-info-data";
import {TaskService} from "@generated/task-service";
import {EvacuationContext} from "@navigation/context/evacuation.context";
import {RefreshHandler} from "@service/global-refresh.service";
import {ManagedObject} from "@service/managed-object";
import {ScreenReaderAnnouncer} from "@service/screen-reader-announcer.service";
import {SpaceEfficiencyService} from "@service/space-efficiency-service";
import {TaskStatus} from "@service/task-monitor-service";
import {Logger} from "@util/logger";
import {VsanDataMigrationUtil} from "@util/vsan-data-migration-util";
import {VsanUiUtils} from "@util/vsan-util";
import {PrecheckUtil} from "./precheck-util";
import {PrecheckReportService} from "./report/precheck-report.service";

@Component({
   templateUrl: "precheck.html",
   styleUrls: ["precheck.scss"],
   selector: "vsan-precheck",
})
export class PrecheckComponent implements OnInit {
   private static readonly TIMEOUT_IN_MS: number = 2000;

   private static readonly NEED_REFRESH_ACTIONS: PrecheckTaskType[] = [
      PrecheckTaskType.HOST_ENTER_MAINTENANCE_MODE,
      PrecheckTaskType.DISKGROUP_REMOVAL,
      PrecheckTaskType.DISKGROUP_UNMOUNT,
      PrecheckTaskType.DISKGROUP_RECREATE,
      PrecheckTaskType.DISK_REMOVAL,
      PrecheckTaskType.DISK_UNMOUNT,
   ];

   clusterRef: ManagedObject = ManagedObject.contextObject;
   loading: boolean;
   alertMessage: string;
   spaceEfficiencyConfig: SpaceEfficiencyConfig;
   isHostResourcePrecheckSupported: boolean;
   isDiskResourcePrecheckSupported: boolean;
   precheckEntities: PrecheckEntity[];
   precheckEntitiesTreeNodes: TreeNode[];
   selectedPrecheckEntities: PrecheckEntity[];
   selectedDecommissionMode: DecommissionMode;
   diskGroupOfSelectedDisk: PrecheckEntity;
   precheckUnsupportedLabel: string;
   precheckTask: PrecheckTaskData;
   taskProgress: number;
   showReport: boolean;
   // whether we should display information for the task progress
   // Changed when task is started, finished and on selection change.
   isPrecheckInProgress: boolean;
   // If there is another operation on the cluster the "Pre-check" button is disabled
   isClusterOperationInProgress: boolean;
   // Whether the running task is for the selected entity.
   // Changed on task started and on selection change.
   isRunningTaskForSelectedEntity: boolean;
   // Whether the pre-check report is refreshed, mark it to false to load report.
   reportRefreshed: boolean = false;

   unavailableView: UnavailableViewSpec;
   hasEditPrivilege: boolean;
   isVsanMaxEnabled: boolean;

   @ScreenReaderAnnouncer()
   srOnlyMessage: string;

   get selectedPrecheckEntity(): PrecheckEntity {
      return VsanUiUtils.isEmpty(this.selectedPrecheckEntities)
            ? null
            : this.selectedPrecheckEntities[0];
   }

   set selectedPrecheckEntity(entity: PrecheckEntity) {
      this.selectedPrecheckEntities = entity
            ? [entity]
            : [];
      this.precheckUnsupportedLabel = this.getPrecheckUnsupportedLabel();
      // Update disk group if new entity is a capacity disk.
      this.diskGroupOfSelectedDisk = !!entity && entity.type === PrecheckEntityType.DISK
            ? PrecheckUtil.extractParentEntityByUuid(entity.uuid, this.precheckEntities)
            : null;
   }

   constructor(private precheckService: PrecheckService,
         private precheckReportService: PrecheckReportService,
         private taskService: TaskService,
         private permissionService: PermissionService,
         private spaceEfficiencyService: SpaceEfficiencyService) {
   }

   ngOnInit(): void {
      this.loading = true;
      this.permissionService.hasPermissions(this.clusterRef, [Privileges.EDIT_CLUSTER()])
            .then(result => this.hasEditPrivilege = result)
            .catch(error => {
               Logger.warn("Unable to check edit cluster privilege: ", error);
            });

      const context: EvacuationContext = VsanUiUtils.getNavigationData();
      if (context) {
         if (context.selectedEntity) {
            this.selectedPrecheckEntity = PrecheckEntity.Factory.create(
                  ManagedObject.fromUid(context.selectedEntity), null, null, null, null, true, false, null, null, null);
            if (context.objectUuid) {
               this.selectedPrecheckEntity.uuid = context.objectUuid;
            }
            if (context.selectedEvacuationType) {
               this.selectedDecommissionMode = context.selectedEvacuationType;
            }
         } else {
            Logger.error("Missing selected entity when context is populated for Data migration pre-check view");
         }
      }
      this.refresh();
   }

   @RefreshHandler
   refresh(): void {
      this.loading = true;
      this.unavailableView = null;

      this.precheckService.getPrecheckData(this.clusterRef)
            .then(this.onPrecheckRetrieved)
            .catch(this.onError)
            .finally(() => this.loading = false);
   }

   onPrecheckEntityChange(entity: PrecheckEntity): void {
      this.selectedPrecheckEntity = entity;
      this.selectedDecommissionMode = DecommissionMode.ensureObjectAccessibility;
      // show report only when the resource pre-check can be run for the selected entity.
      this.showReport = this.precheckSupported;
      this.reportRefreshed = false;
      this.isPrecheckInProgress = false;
      this.isRunningTaskForSelectedEntity = false;
   }

   get precheckSupported(): boolean {
      return this.selectedPrecheckEntity && !this.precheckUnsupportedLabel;
   }

   onDecommissionModeSummaryChanged(event: DecommissionMode): void {
      this.selectedDecommissionMode = event;
      this.reportRefreshed = false;
      this.showReport = true;
      this.isPrecheckInProgress = false;
      this.isRunningTaskForSelectedEntity = false;
   }

   onReportRefreshed(): void {
      this.reportRefreshed = true;
   }

   // override this flag only when it is regarding another entity in the cluster
   onHasRunningTask(event: boolean): void {
      if (!this.isRunningTaskForSelectedEntity) {
         this.isClusterOperationInProgress = event;
      }
   }

   async onPrecheckClicked(): Promise<void> {
      try {
         let taskType: PrecheckTaskType;
         let operation: PrecheckOperation;
         switch (this.selectedPrecheckEntity.type) {
            case PrecheckEntityType.HOST:
               taskType = PrecheckTaskType.HOST_PRECHECK;
               operation = PrecheckOperation.ENTER_MAINTENANCE_MODE;
               break;
            case PrecheckEntityType.DISK_GROUP:
               taskType = PrecheckTaskType.DISKGROUP_PRECHECK;
               operation = PrecheckOperation.DISK_DATA_EVACUATION;
               break;
            case PrecheckEntityType.DISK:
               taskType = PrecheckTaskType.DISK_PRECHECK;
               operation = PrecheckOperation.DISK_DATA_EVACUATION;
               break;
         }

         const precheckTaskRef: ManagedObject = await this.precheckService.runDataMigrationPrecheck(
               this.clusterRef,
               this.selectedPrecheckEntity.uuid,
               this.selectedDecommissionMode.toString(),
               operation);
         this.precheckTask = PrecheckTaskData.Factory.create(
               precheckTaskRef, // task MoRef
               this.selectedPrecheckEntity.name, // entity name
               this.selectedPrecheckEntity.uuid, // enitty uuid
               null, // host name
               this.selectedDecommissionMode, // decommission mode
               taskType); // pre-check type
         await this.onPrecheckTaskStarted(this.precheckTask);
      } catch (error) {
         this.onError(error);
      }
   }

   async onPrecheckTaskStarted(precheckTaskData: PrecheckTaskData): Promise<void> {
      this.alertMessage = undefined;
      if (!precheckTaskData) {
         return;
      }

      this.showReport = false;
      this.isRunningTaskForSelectedEntity = this.precheckReportService.isTaskForSelectedEntity(
            precheckTaskData, this.selectedPrecheckEntity.uuid, this.selectedDecommissionMode);
      this.isClusterOperationInProgress = true;
      if (!this.isRunningTaskForSelectedEntity) {
         // show the report if the selection is changed
         this.showReport = true;
         return;
      }

      this.taskProgress = 0;
      this.isPrecheckInProgress = true;
      this.precheckTask = precheckTaskData;
      // Await task completion
      try {
         while (true) {
            // do not track the progress if the selection is changed
            if (!this.isPrecheckInProgress) {
               return;
            }

            const taskInfo: TaskInfoData = await this.taskService.getInfo(this.precheckTask.taskMoRef);
            if (!taskInfo || taskInfo.status === TaskStatus.SUCCESS) {
               break;
            }

            if (taskInfo.status === TaskStatus.ERROR) {
               const errorMessage: string = this.precheckReportService.getFailedTaskMessage(
                     this.precheckTask.taskType, this.selectedPrecheckEntity.name);
               this.onError(errorMessage);
               return;
            }

            this.taskProgress = taskInfo.progress;
            // repeat on every 2 seconds
            await new Promise(resolve => setTimeout(resolve, PrecheckComponent.TIMEOUT_IN_MS));
         }
      } catch (error) {
         this.onError(error);
         return;
      }

      this.isPrecheckInProgress = false;
      this.isClusterOperationInProgress = false;

      const refreshNeeded: boolean =
            PrecheckComponent.NEED_REFRESH_ACTIONS.indexOf(precheckTaskData.taskType) > -1;
      if (refreshNeeded) {
         this.selectedPrecheckEntity = undefined;
         this.refresh();
      } else {
         this.showReport = true;
         this.reportRefreshed = false;
      }
   }

   onSelectedObjectInfosChange(objectInfos: ObjectInfo[]): void {
      if (VsanUiUtils.isEmpty(objectInfos) ||
            (this.selectedPrecheckEntity && objectInfos[0].uuid === this.selectedPrecheckEntity.uuid)) {
         return;
      }
      this.onPrecheckEntityChange(objectInfos[0] as PrecheckEntity);
      this.selectedPrecheckEntities = objectInfos as PrecheckEntity[];
   }

   private convertToTreeNodes(entities: PrecheckEntity[]): TreeNode[] {
      if (VsanUiUtils.isEmpty(entities)) {
         return [];
      }
      return entities.map(entity => new TreeNode(entity, this.convertToTreeNodes(entity.children)));
   }

   private getPrecheckUnsupportedLabel(): string {
      if (!this.selectedPrecheckEntity) {
         return "";
      }
      switch (this.selectedPrecheckEntity.type) {
         case PrecheckEntityType.HOST:
            if (!this.selectedPrecheckEntity.isHostConnected) {
               return VsanUiUtils.getString("vsan.dataMigrationPrecheck.notSupported.host.disconnected");
            } else if (this.selectedPrecheckEntity.isInMaintenanceMode) {
               return VsanUiUtils.getString("vsan.dataMigrationPrecheck.notSupported.host.maintenanceMode");
            }
            break;
         case PrecheckEntityType.DISK_GROUP:
            if (this.selectedPrecheckEntity.isInMaintenanceMode) {
               return VsanUiUtils.getString("vsan.dataMigrationPrecheck.notSupported.diskgroup.maintenanceMode");
            }
            if (this.selectedPrecheckEntity.diskGroupData) {
               if (this.selectedPrecheckEntity.diskGroupData.isLocked) {
                  return VsanUiUtils.getString("vsan.dataMigrationPrecheck.notSupported.diskgroup.locked");
               } else if (!this.selectedPrecheckEntity.diskGroupData.isMounted) {
                  return VsanUiUtils.getString("vsan.dataMigrationPrecheck.notSupported.diskgroup.unmounted");
               }
            }
            break;
         case PrecheckEntityType.DISK:
            if (this.selectedPrecheckEntity.isInMaintenanceMode) {
               return VsanUiUtils.getString("vsan.dataMigrationPrecheck.notSupported.disk.maintenanceMode");
            }
            if (!this.spaceEfficiencyService.isDiskPreCheckEvacuationSupported(this.spaceEfficiencyConfig)) {
               return VsanUiUtils.getString("vsan.dataMigrationPrecheck.notSupported.disk.dedupEnabled");
            }
            if (this.selectedPrecheckEntity.diskData) {
               if (this.selectedPrecheckEntity.diskData.deviceState === DiskData.DeviceState.OFF) {
                  return this.isVsanMaxEnabled
                        ? VsanUiUtils.getString("vsan.dataMigrationPrecheck.notSupported.disk.unmounted")
                        : VsanUiUtils.getString("vsan.dataMigrationPrecheck.notSupported.disk.unmountedDiskGroup");
               } else if (this.selectedPrecheckEntity.diskData.deviceState === DiskData.DeviceState.ERROR) {
                  return VsanUiUtils.getString("vsan.dataMigrationPrecheck.notSupported.disk.deadOrError");
               }
            }
            break;
         default:
            return "";
      }
   }

   getOperationInProgressLabel(): string {
      if (!this.precheckTask) {
         return VsanUiUtils.getString("vsan.evacuationStatus.report.inProgress", this.taskProgress);
      }

      switch (this.precheckTask.taskType) {
         case PrecheckTaskType.HOST_ENTER_MAINTENANCE_MODE:
            return VsanUiUtils.getString("vsan.evacuationStatus.report.inProgress.maintenanceMode", this.taskProgress);
         case PrecheckTaskType.DISKGROUP_REMOVAL:
            return VsanUiUtils.getString("vsan.evacuationStatus.report.inProgress.removeGroup", this.taskProgress);
         case PrecheckTaskType.DISKGROUP_RECREATE:
            return VsanUiUtils.getString("vsan.evacuationStatus.report.inProgress.recreateGroup", this.taskProgress);
         case PrecheckTaskType.DISKGROUP_UNMOUNT:
            return VsanUiUtils.getString("vsan.evacuationStatus.report.inProgress.unmountGroup", this.taskProgress);
         case PrecheckTaskType.DISK_REMOVAL:
            return VsanUiUtils.getString("vsan.evacuationStatus.report.inProgress.removeDisk", this.taskProgress);
         case PrecheckTaskType.DISK_UNMOUNT:
            return VsanUiUtils.getString("vsan.evacuationStatus.report.inProgress.unmountDisk", this.taskProgress);
         default:
            // Running data migration pre-check for host, disk group or disk.
            return VsanUiUtils.getString("vsan.evacuationStatus.report.inProgress", this.taskProgress);
      }
   }

   getGeneralMessageLabel(): string {
      if (this.isVsanMaxEnabled) {
         return VsanUiUtils.getString("vsan.evacuationStatus.hostDisk.resourcePrecheck.generalMessage");
      }

      return this.isDiskResourcePrecheckSupported
            ? VsanUiUtils.getString("vsan.evacuationStatus.hostDiskGroupDisk.resourcePrecheck.generalMessage")
            : VsanUiUtils.getString("vsan.evacuationStatus.host.resourcePrecheck.generalMessage");
   }

   private onPrecheckRetrieved = (result: PrecheckData) => {
      if (!result) {
         return;
      }

      if (!result.isHostResourcePrecheckSupported) {
         this.unavailableView = UnavailableViewSpec.DATA_MIGRATION_PRECHECK_VIEW_OLD_HOSTS;
         this.announceToScreenReader();
         return;
      }

      if (VsanUiUtils.isEmpty(result.precheckEntities)) {
         this.unavailableView = UnavailableViewSpec.DATA_MIGRATION_PRECHECK_VIEW_NO_HOSTS;
         this.announceToScreenReader();
         return;
      }

      this.alertMessage = result.errorMessage;
      this.spaceEfficiencyConfig = result.spaceEfficiencyConfig;
      this.isHostResourcePrecheckSupported = result.isHostResourcePrecheckSupported;
      this.isDiskResourcePrecheckSupported = result.isDiskResourcePrecheckSupported;
      this.isVsanMaxEnabled = result.isVsanMaxEnabled;

      this.precheckEntities = result.precheckEntities;
      this.precheckEntitiesTreeNodes = this.convertToTreeNodes(this.precheckEntities);
      this.selectPrecheckEntity();

      if (this.selectedDecommissionMode === undefined || this.selectedDecommissionMode === null) {
         this.selectedDecommissionMode = DecommissionMode.ensureObjectAccessibility;
      }
      this.showReport = this.precheckSupported;
      this.reportRefreshed = false;

      this.announceToScreenReader();
   };

   private selectPrecheckEntity(): void {
      if (!this.selectedPrecheckEntity || VsanUiUtils.isEmpty(this.precheckEntities)) {
         return;
      }

      if (this.selectedPrecheckEntity.uuid) {
         // Update selected evacuation entity based on uuid after refreshing entities.
         this.selectedPrecheckEntity = PrecheckUtil.extractPrecheckEntityByUuid(
               this.selectedPrecheckEntity.uuid, this.precheckEntities);
      } else {
         // When we navigate the user from Enter Maintenance Mode dialog to this page, it only passes hostRef,
         // we need to use the hostRef to extract the entire host entity data.
         this.selectedPrecheckEntity = PrecheckUtil.extractHostEntity(
               this.selectedPrecheckEntity.hostRef, this.precheckEntities);
      }
   }

   private announceToScreenReader(): void {
      if (this.selectedPrecheckEntity && this.selectedDecommissionMode) {
         this.srOnlyMessage = VsanUiUtils.getString("vsan.evacuationStatus.report.srMessage",
               this.selectedPrecheckEntity.name,
               VsanDataMigrationUtil.getLocalizedDecommissionMode(this.selectedDecommissionMode));
      } else {
         this.srOnlyMessage = VsanUiUtils.getString("vsan.screenReader.message.page.loaded",
               VsanUiUtils.getString("vsan.monitor.evacuationStatus.label"));
      }
   }

   private onError = (error: any) => {
      this.alertMessage = error;
      this.isRunningTaskForSelectedEntity = false;
      this.isPrecheckInProgress = false;
      this.isClusterOperationInProgress = false;
      // even when the task fails we want to show the last valid report
      this.showReport = true;
      this.reportRefreshed = false;
   };
}
