import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {PrecheckEntity} from "@generated/precheck-entity";
import {PrecheckEntityType} from "@generated/precheck-entity-type";

export class PrecheckUtil {
   /**
    * Extracts the specified precheck entity from entities based on uuid.
    */
   static extractPrecheckEntityByUuid = (uuid: string, entities: PrecheckEntity[]): PrecheckEntity => {
      if (!uuid || VsanUiUtils.isEmpty(entities)) {
         return null;
      }

      for (let index: number = 0; index < entities.length; index++) {
         if (entities[index].uuid === uuid) {
            return entities[index];
         } else {
            const selectedEntity: PrecheckEntity = PrecheckUtil.extractPrecheckEntityByUuid(
                  uuid, entities[index].children);
            if (selectedEntity) {
               return selectedEntity;
            }
         }
      }

      return null;
   };

   /**
    * Extracts the specified precheck host entity from entities based on host reference.
    */
   static extractHostEntity = (hostRef: ManagedObject, entities: PrecheckEntity[]): PrecheckEntity => {
      if (!hostRef || VsanUiUtils.isEmpty(entities)) {
         return null;
      }
      return entities.find(entity => entity.type === PrecheckEntityType.HOST &&
            ManagedObject.areEqual(hostRef, entity.hostRef));
   };

   /**
    * Extracts the parent entity of the specified uuid.
    */
   static extractParentEntityByUuid = (uuid: string, entities: PrecheckEntity[]): PrecheckEntity => {
      if (!uuid || VsanUiUtils.isEmpty(entities)) {
         return null;
      }

      for (let parentIndex: number = 0; parentIndex < entities.length; parentIndex++) {
         const children: PrecheckEntity[] = entities[parentIndex].children;
         if (!children || children.length === 0) {
            continue;
         }

         if (children.find(element => uuid === element.uuid)) {
            return entities[parentIndex];
         }

         const parentOfGrandChild: PrecheckEntity = PrecheckUtil.extractParentEntityByUuid(uuid, children);
         if (parentOfGrandChild) {
            return parentOfGrandChild;
         }
      }

      return null;
   };
}