import {Component, Input} from "@angular/core";
import {AlertType} from "@component/validation/alert-type";
import {DatastoreType} from "@generated/datastore-type";
import {PrecheckClusterCapacityData} from "@generated/precheck-cluster-capacity-data";
import {PrecheckResultStatusType} from "@generated/precheck-result-status-type";
import {KnowledgeBaseId} from "@service/navigation/model/knowledge-base-id";
import {VsanUiUtils} from "@util/vsan-util";
import {PrecheckReportService} from "../precheck-report.service";

@Component({
   templateUrl: "precheck-capacity-tab.html",
   styleUrls: ["precheck-capacity-tab.scss"],
   selector: "vsan-precheck-capacity-tab",
})
export class PrecheckCapacityTabComponent {
   CapacityReport: typeof CapacityReport = CapacityReport;
   AlertType: typeof AlertType = AlertType;
   DatastoreType: typeof DatastoreType = DatastoreType;
   readonly KnowledgeBaseId: typeof KnowledgeBaseId = KnowledgeBaseId;

   @Input()
   capacityReports: Map<CapacityReport, PrecheckClusterCapacityData>;

   @Input()
   hasPersistenceDataToRebuild: boolean;

   @Input()
   hasError: boolean;

   currentCapacityReport: CapacityReport = CapacityReport.VSAN;

   constructor(private precheckReportService: PrecheckReportService) {
   }

   get vsanCapacity(): PrecheckClusterCapacityData {
      return this.capacityReports.get(CapacityReport.VSAN);
   }

   get vsanDirectCapacity(): PrecheckClusterCapacityData {
      return this.capacityReports.get(CapacityReport.VSAN_DIRECT);
   }

   // Capacity information is shown when there is a vSAN capacity error with recommendations
   // or when there is no vSAN direct capacity
   // or there is vSAN direct capacity without an error
   showCapacity(): boolean {
      return this.vsanCapacity.status === PrecheckResultStatusType.RED
            || !(this.vsanDirectCapacity && this.vsanDirectCapacity.status === PrecheckResultStatusType.RED);
   }

   showVsanDirectCapacity(): boolean {
      return !!this.vsanDirectCapacity
            && VsanUiUtils.isEmpty(this.vsanDirectCapacity.errorMessages)
            && this.vsanCapacity.status !== PrecheckResultStatusType.RED;
   }

   getClusterLevelErrorMessageLabel = (): string => {
      return this.precheckReportService.getClusterLevelCapacityErrorMessage(
            this.capacityReports.get(CapacityReport.VSAN));
   };
}

export enum CapacityReport {
   VSAN,
   VSAN_DIRECT,
}