import {PrecheckTaskType} from '@generated/precheck-task-type';
import {DecommissionMode} from '@generated/decommission-mode';

export interface PrecheckTaskData {

   taskMoRef: any;
   name: string;
   uuid: string;
   hostName: string;
   decommissionMode: DecommissionMode;
   taskType: PrecheckTaskType;
} //class PrecheckTaskData

export module PrecheckTaskData{
export class Factory {

   static create = (taskMoRef: any, name: string, uuid: string, hostName: string, decommissionMode: DecommissionMode, taskType: PrecheckTaskType): PrecheckTaskData => {
      return { taskMoRef: taskMoRef, name: name, uuid: uuid, hostName: hostName, decommissionMode: decommissionMode, taskType: taskType, };
   }


} //class Factory

} //module PrecheckTaskData
